import "regenerator-runtime"; /* for async await transpile */
import "../styles/main.css";
import Swal from "sweetalert2"; // Import diletakkan di bagian paling atas

// import "../public/manifest.json";

const RESTAURANT_API_ENDPOINT = "https://restaurant-api.dicoding.dev";

// Fungsi untuk mengambil daftar restoran dari API
async function fetchRestaurantList() {
  showLoader(); // Tampilkan loader saat memulai pemuatan
  try {
    const response = await fetch(`${RESTAURANT_API_ENDPOINT}/list`);
    if (!response.ok) {
      throw new Error("Gagal mendapatkan data restoran");
    }
    const jsonData = await response.json();
    return jsonData.restaurants;
  } catch (error) {
    Swal.fire({
      icon: "error",
      title: "Terjadi Kesalahan",
      text: "Gagal mendapatkan data restoran. Silakan coba lagi.",
    }); // Menampilkan pesan kesalahan
    console.error("Error:", error);
    return [];
  } finally {
    hideLoader(); // Sembunyikan loader setelah pemuatan selesai
  }
}

// Definisikan komponen web kustom untuk menampilkan daftar restoran
class RestaurantList extends HTMLElement {
  async connectedCallback() {
    this.innerHTML = "<div>Loading...</div>"; // Loading placeholder

    const restaurantList = await fetchRestaurantList(); // Fetch the list of restaurants
    this.renderList(restaurantList);
  }

  renderList(restaurants) {
    // Start the grid container
    this.innerHTML = `<div class="restaurant-grid"></div>`;
    const gridContainer = this.querySelector(".restaurant-grid");

    // For each restaurant, create a grid item
    restaurants.forEach((restaurant) => {
      const restaurantElement = document.createElement("div");
      restaurantElement.classList.add("restaurant-item"); // Add a class for styling

      restaurantElement.innerHTML = `
      <div class="list_item">
      <img 
        class="list_item_thumb" 
        src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" 
        alt="${restaurant.name}" 
        loading="lazy"
      />
      <div class="city">${restaurant.city}</div>
      <div class="list_item_content">
        <div class="list_item_rating">
          <span class="star-icon">&#9733;</span> 
          ${restaurant.rating}
        </div>
        <h1 class="list_item_title">
        <a href="#detail-${restaurant.id}" data-id="${restaurant.id}">${
        restaurant.name
      }</a>
        </h1>
        <div class="list_item_desc">${restaurant.description.slice(
          0,
          150
        )}...</div>
      </div>
    </div>
  `;

      // Append each restaurant to the grid container
      gridContainer.appendChild(restaurantElement);
    });
  }
}

// Mendaftarkan elemen kustom
customElements.define("restaurant-list", RestaurantList);

// Fungsi untuk mendapatkan ID restoran dari hash
export function getRestaurantIdFromHash() {
  const hash = window.location.hash;
  if (hash.startsWith("#detail-")) {
    return hash.split("-")[1]; // Mengambil ID dari hash
  }
  return null;
}


// Pada saat DOM sudah siap, cek apakah ada ID restoran dari hash
document.addEventListener("DOMContentLoaded", async () => {
  const RestaurantList = document.querySelector("#RestaurantList"); // Pastikan elemen ini ada di HTML
  const restaurantId = getRestaurantIdFromHash();
  console.log(
    "RestaurantList Display:",
    getComputedStyle(RestaurantList).display
  );
  console.log(
    "Grid Columns:",
    getComputedStyle(RestaurantList).gridTemplateColumns
  );

  if (restaurantId) {
    await fetchRestaurantDetail(restaurantId); // Ambil detail restoran berdasarkan ID
  } else {
    const restaurantListElement = document.createElement("restaurant-list"); // Buat elemen kustom untuk daftar restoran
    RestaurantList.appendChild(restaurantListElement); // Tambahkan ke kontainer
  }

  // Fungsi untuk menangani perubahan hash
  async function handleHashChange() {
    showLoader(); // Tampilkan loader saat berpindah halaman
    const hash = window.location.hash;
    const restaurantId = getRestaurantIdFromHash();
    const restaurantListElement = document.querySelector("#RestaurantList");
    const detailElement = document.querySelector("#restaurantDetail");
    const favoriteRestaurantList = document.querySelector(
      "#favoriteRestaurantList"
    );
    // Sembunyikan semua bagian
    restaurantListElement.style.display = "none";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "none";
    if (window.location.hash === "") {
      await renderRestaurantList(); // Render ulang daftar restoran jika hash kosong
    }
    if (hash === "#favorite") {
      const favoriteRestaurantListElement = document.createElement(
        "favorite-restaurant-list"
      );
      favoriteRestaurantList.innerHTML = ""; // Kosongkan konten sebelumnya
      favoriteRestaurantList.appendChild(favoriteRestaurantListElement); // Tambahkan komponen daftar restoran favorit

      // Sembunyikan elemen yang tidak diperlukan dan tampilkan elemen favorit
      restaurantListElement.style.display = "none";
      detailElement.style.display = "none";
      favoriteRestaurantList.style.display = "grid";

      hideLoader(); // Sembunyikan loader saat selesai memuat
    } else if (hash.startsWith("#detail-")) {
      const restaurantId = getRestaurantIdFromHash();
      await fetchRestaurantDetail(restaurantId);
      restaurantListElement.style.display = "none";
      detailElement.style.display = "block";
      favoriteRestaurantList.style.display = "none"; // Sembunyikan halaman favorit
  

      hideLoader(); // Sembunyikan loader saat selesai memuat
    } else {
      restaurantListElement.style.display = "grid";
      detailElement.style.display = "none";
      favoriteRestaurantList.style.display = "none"; // Sembunyikan halaman favorit
    }
    if (restaurantId) {
      // Jika hash mengarah ke detail restoran, tampilkan detail
      const restaurantDetail = await fetchRestaurantDetail(restaurantId);

      if (restaurantDetail) {
        detailElement.innerHTML = ""; // Kosongkan detail sebelumnya
        const restaurantDetailElement =
          document.createElement("restaurant-detail");
        restaurantDetailElement.setAttribute("restaurant-id", restaurantId);

        detailElement.appendChild(restaurantDetailElement); // Tambahkan elemen detail
        detailElement.style.display = "block"; // Pastikan detail terlihat
        restaurantListElement.style.display = "none"; // Sembunyikan daftar restoran

        hideLoader(); // Sembunyikan loader saat selesai memuat
      } else {
        console.error("Detail restoran tidak ditemukan");
        window.location.hash = ""; // Jika tidak ada ID, kembali ke daftar restoran
      }
    } else {
      restaurantListElement.style.display = "none";
      favoriteRestaurantList.style.display = "grid";
      detailElement.style.display = "none"; // Sembunyikan detail

      hideLoader(); // Sembunyikan loader saat selesai memuat
    }
  }

  // Pasang event listener untuk hashchange dan DOMContentLoaded
  window.addEventListener("hashchange", handleHashChange);
  document.addEventListener("DOMContentLoaded", async () => {
    console.log("Page loaded. Current hash:", window.location.hash);
    await handleHashChange(); // Pastikan tampilan awal sesuai dengan hash
  });
});

// Fungsi untuk menampilkan halaman berdasarkan hash
async function renderContentBasedOnHash() {
  showLoader(); // Tampilkan loader saat berpindah halaman
  const hash = window.location.hash;
  const restaurantListElement = document.querySelector("#RestaurantList");
  const detailElement = document.querySelector("#restaurantDetail");
  const favoriteRestaurantList = document.querySelector(
    "#favoriteRestaurantList"
  );

  if (hash.startsWith("#detail-")) {
    const restaurantId = getRestaurantIdFromHash();
    if (restaurantId) {
      // Ambil detail restoran dan tampilkan
      const restaurant = await fetchRestaurantDetail(restaurantId);
      if (restaurant) {
        detailElement.innerHTML = ""; // Kosongkan konten sebelumnya
        const restaurantDetailElement =
          document.createElement("restaurant-detail");
        restaurantDetailElement.setAttribute("restaurant-id", restaurantId);
        detailElement.appendChild(restaurantDetailElement);

        detailElement.style.display = "block";
        restaurantListElement.style.display = "none";
        hideLoader(); // Sembunyikan loader saat selesai memuat
      } else {
        console.error("Detail restoran tidak ditemukan");
      }
    }
  } else {
    // Default behavior
    restaurantListElement.style.display = "grid";
    detailElement.style.display = "none";
    hideLoader(); // Sembunyikan loader saat selesai memuat
  }
  if (hash === "#favorite") {
    showLoader(); // Tampilkan loader saat berpindah halaman
    // Tampilkan halaman favorit
    const favoriteRestaurantListElement = document.createElement(
      "favorite-restaurant-list"
    );
    favoriteRestaurantList.innerHTML = ""; // Kosongkan konten sebelumnya
    favoriteRestaurantList.appendChild(favoriteRestaurantListElement); // Tambahkan komponen daftar restoran favorit

    restaurantListElement.style.display = "none";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "block"; // Tampilkan halaman favorit
    hideLoader(); // Sembunyikan loader saat selesai memuat
  } else if (hash.startsWith("#detail-")) {
    const restaurantId = getRestaurantIdFromHash();
    await fetchRestaurantDetail(restaurantId); // Ambil detail restoran
    restaurantListElement.style.display = "none";
    detailElement.style.display = "block";
    favoriteRestaurantList.style.display = "none"; // Sembunyikan halaman favorit
    hideLoader(); // Sembunyikan loader saat selesai memuat
  } else {
    restaurantListElement.style.display = "grid";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "none"; // Sembunyikan halaman favorit
    hideLoader(); // Sembunyikan loader saat selesai memuat
  }
}
// Pasang event listener untuk `hashchange`
window.addEventListener("hashchange", renderContentBasedOnHash);

const renderRestaurantList = async () => {
  const restaurantListElement = document.querySelector("#RestaurantList");
  if (restaurantListElement) {
    const restaurantList = await fetchRestaurantList(); // Ambil data daftar restoran
    restaurantListElement.innerHTML = ""; // Kosongkan konten sebelumnya
    const restaurantListComponent = document.createElement("restaurant-list");
    restaurantListComponent.renderList(restaurantList); // Memastikan render ulang
    restaurantListElement.appendChild(restaurantListComponent);
  }
};

// Event listener untuk `DOMContentLoaded` untuk menangani refresh
document.addEventListener("DOMContentLoaded", async () => {
  console.log("Page loaded. Current hash:", window.location.hash);
  await renderContentBasedOnHash(); // Pastikan ini adalah fungsi async
});

document.addEventListener("click", async (event) => {
  const favoriteButton = event.target.closest("#favoriteButton");

  if (favoriteButton) {
    const restaurantId = favoriteButton.getAttribute("data-id");
    const isFavorited = await isRestaurantFavorited(restaurantId);

    if (isFavorited) {
      await removeRestaurantFromFavorites(restaurantId); // Hapus dari favorit
      favoriteButton.classList.remove("favorited");
      favoriteButton.innerHTML = '<i class="far fa-heart"></i>'; // Ikon hati kosong
    } else {
      // Ambil data lengkap tentang restoran
      const restaurant = await fetchRestaurantDetail(restaurantId);

      if (restaurant) {
        await addRestaurantToFavorites(restaurant); // Simpan data lengkap ke IndexedDB
        favoriteButton.classList.add("favorited");
        favoriteButton.innerHTML = '<i class="fas fa-heart"></i>'; // Ikon hati penuh
      }
    }
  }
});

