const fetchRestaurantDetail = async (restaurantId) => {
  showLoader(); // Tampilkan loader saat memulai pemuatan
  try {
    const response = await fetch(
      `${RESTAURANT_API_ENDPOINT}/detail/${restaurantId}`
    );
    if (!response.ok) {
      throw new Error("Gagal mendapatkan detail restoran");
    }

    const data = await response.json();
    return data.restaurant; // Kembalikan informasi lengkap
  } catch (error) {
    console.error("Gagal mendapatkan detail restoran:", error);
    return null;
  } finally {
    hideLoader(); // Sembunyikan loader setelah pemuatan selesai
  }
};

class RestaurantDetail extends HTMLElement {
  constructor() {
    super();
    this.restaurantId = null;
  }

  async connectedCallback() {
    this.restaurantId = this.getAttribute("restaurant-id");
    const restaurant = await fetchRestaurantDetail(this.restaurantId);

    if (restaurant) {
      console.log("Menampilkan detail restoran:", restaurant); // Logging detail
      await this.renderRestaurantDetail(restaurant);
    }
  }

  async renderRestaurantDetail(restaurant) {
    // Logika untuk menampilkan detail restoran
    console.log("Mengatur konten untuk detail restoran"); // Logging
    const categories = restaurant.categories
      .map((category) => category.name)
      .join(", ");
    const foods = restaurant.menus.foods.map((food) => food.name).join(", ");
    const drinks = restaurant.menus.drinks
      .map((drink) => drink.name)
      .join(", ");
    const reviewsContent = restaurant.customerReviews
      .map(
        (review) => `
      <div class="customer-review">
        <p><strong>${review.name}</strong> pada ${review.date}</p>
        <p>${review.review}</p>
      </div>
    `
      )
      .join("");

    const isFavorited = await isRestaurantFavorited(this.restaurantId);
    const favoriteButtonIcon = isFavorited ? "fas fa-heart" : "far fa-heart"; // Hati penuh atau kosong

    // Tombol kembali ke daftar restoran
    const backButton = `
      <button id="backButton" class="back-button" aria-label="Kembali ke daftar restoran"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M19 12H5" stroke="#fff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"/>
      <path d="M12 19L5 12L12 5" stroke="#fff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"/>
    </svg></button>
    `;

    const favoriteButton = `
      <button class="favorite-button" id="favoriteButton" data-id="${this.restaurantId}">
        <i class="${favoriteButtonIcon}"></i>
      </button>
    `;

    const reviewForm = `
      <div id="reviewFormContainer">
        <h3>Tambahkan Ulasan</h3>
        <form id="reviewForm">
          <input type="hidden" name="restaurantId" value="${this.restaurantId}">
          <div>
            <label for="reviewName">Nama:</label>
            <input type="text" id="reviewName" name="name" required>
          </div>
          <div>
            <label for="reviewText">Ulasan:</label>
            <textarea id="reviewText" name="review" required></textarea>
          </div>
          <button type="submit">Kirim Ulasan</button>
        </form>
      </div>
    `;

    // Set content untuk detail restoran
    this.innerHTML = `
      <h1>Detail Restoran</h1>
      ${backButton} <!-- Tambahkan tombol kembali -->
      <img src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" alt="${restaurant.name}">
      <h2>${restaurant.name}</h2>
      <div class="detailatas">
        <p><strong>Alamat:</strong> ${restaurant.address}</p>
        <p><strong>Kota:</strong> ${restaurant.city}</p>
        <p><strong>Kategori:</strong> ${categories}</p>
        <p><strong>Description:</strong> ${restaurant.description}</p>
        <p><strong>Menu Makanan:</strong> ${foods}</p>
        <p><strong>Menu Minuman:</strong> ${drinks}</p>
        ${favoriteButton}
      </div>
      ${reviewForm}
      <div class="ulasanresto">${reviewsContent}</div>
    `;

    // Tambahkan event listener untuk tombol kembali
    this.querySelector("#backButton").addEventListener("click", () => {
      window.location.hash = ""; // Kembali ke daftar restoran
      location.reload(); // Menyegarkan ulang halaman
    });

    // Tambahkan event listener untuk tombol favorit
    this.querySelector("#favoriteButton").addEventListener(
      "click",
      async (event) => {
        const button = event.target;
        const isFavorited = await isRestaurantFavorited(this.restaurantId);

        if (isFavorited) {
          await removeRestaurantFromFavorites(this.restaurantId);
          button.classList.remove("favorited");
          button.innerHTML = '<i class="far fa-heart"></i>';

          Swal.fire({
            icon: "warning",
            title: "Tidak di Favoritkan!!",
            text: "Restoran telah di hapus dari favorit.",
          }); // Pesan keberhasilan
        } else {
          await addRestaurantToFavorites({ id: this.restaurantId });
          button.classList.add("favorited");
          button.innerHTML = '<i class="fas fa-heart"></i>';

          Swal.fire({
            icon: "success",
            title: "Di Favoritkan!!",
            text: "Restoran telah di favoritkan.",
          }); // Pesan keberhasilan
        }
      }
    );

    // Tambahkan event listener untuk formulir ulasan
    this.querySelector("#reviewForm").addEventListener(
      "submit",
      async (event) => {
        event.preventDefault();
        showLoader(); // Tampilkan loader saat berpindah halaman

        const formData = new FormData(event.target);
        const reviewData = {
          id: formData.get("restaurantId"),
          name: formData.get("name"),
          review: formData.get("review"),
        };

        try {
          const reviewResponse = await fetch(
            `${RESTAURANT_API_ENDPOINT}/review`,
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(reviewData),
            }
          );

          if (reviewResponse.ok) {
            const responseData = await reviewResponse.json();
            const newReview = responseData.customerReviews.at(-1);

            this.querySelector(".ulasanresto").insertAdjacentHTML(
              "beforeend",
              `
                <div class="customer-review">
                  <p><strong>${newReview.name}</strong> pada ${newReview.date}</p>
                  <p>${newReview.review}</p>
                </div>
              `
            ); // Kosongkan formulir ulasan
            event.target.reset();

            Swal.fire({
              icon: "success",
              title: "Ulasan Berhasil",
              text: "Ulasan Anda telah dikirim.",
            }); // Pesan keberhasilan

            console.log("Ulasan berhasil dikirim dan ditambahkan ke halaman.");
            hideLoader(); // Sembunyikan loader saat selesai memuat
          } else {
            console.error("Gagal mengirim ulasan.");
          }
        } catch (error) {
          console.error("Error mengirim ulasan:", error);
          hideLoader(); // Sembunyikan loader saat selesai memuat
        }
      }
    );
  }
}
customElements.define("restaurant-detail", RestaurantDetail);

const DATABASE_NAME = "restaurantDB";
const OBJECT_STORE_NAME = "favorites";

const openDatabase = () => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DATABASE_NAME, 1);

    request.onupgradeneeded = (event) => {
      const db = event.target.result;
      if (!db.objectStoreNames.contains(OBJECT_STORE_NAME)) {
        db.createObjectStore(OBJECT_STORE_NAME, { keyPath: "id" });
      }
    };

    request.onsuccess = (event) => {
      resolve(event.target.result); // Database dibuka dengan benar
    };

    request.onerror = (event) => {
      Swal.fire({
        icon: "error",
        title: "Gagal Membuka Database",
        text: "Terjadi kesalahan saat membuka database. Silakan coba lagi.",
      }); // Pesan kesalahan
      reject(event.target.error); // Tangkap kesalahan saat membuka database
    };
  });
};

// Menambahkan restoran ke favorit
export const addRestaurantToFavorites = async (restaurant) => {
  const db = await openDatabase(); // Buka database
  const transaction = db.transaction(OBJECT_STORE_NAME, "readwrite"); // Transaksi read-write
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);

  try {
    // Simpan data lengkap, bukan hanya ID
    const restaurantData = {
      id: restaurant.id,
      name: restaurant.name,
      pictureId: restaurant.pictureId,
      city: restaurant.city,
      description: restaurant.description,
      rating: restaurant.rating,
    };

    objectStore.put(restaurantData); // Gunakan `put` untuk menghindari duplikasi
    await transaction.complete; // Selesaikan transaksi
    console.log("Restoran ditambahkan ke favorit:", restaurantData); // Logging
  } catch (error) {
    console.error("Error menambahkan restoran ke favorit:", error); // Tangkap kesalahan
  }
};

// Menghapus restoran dari favorit
export const removeRestaurantFromFavorites = async (restaurantId) => {
  const db = await openDatabase();
  const transaction = db.transaction(OBJECT_STORE_NAME, "readwrite");
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);
  objectStore.delete(restaurantId);
  return transaction.complete;
};

// Memeriksa apakah restoran adalah favorit
export const isRestaurantFavorited = async (restaurantId) => {
  const db = await openDatabase();
  const transaction = db.transaction(OBJECT_STORE_NAME, "readonly");
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);
  return new Promise((resolve, reject) => {
    const request = objectStore.get(restaurantId);
    request.onsuccess = () => {
      resolve(request.result !== undefined);
    };
    request.onerror = () => {
      reject(request.error);
    };
  });
};

// Fungsi untuk mengambil semua restoran favorit dari IndexedDB
const fetchFavoriteRestaurants = async () => {
  try {
    const db = await openDatabase(); // Buka database
    const transaction = db.transaction(OBJECT_STORE_NAME, "readonly"); // Transaksi read-only
    const objectStore = transaction.objectStore(OBJECT_STORE_NAME);

    const request = objectStore.getAll(); // Ambil semua data dari Object Store
    return new Promise((resolve, reject) => {
      request.onsuccess = () => {
        resolve(request.result); // Kembalikan data favorit
      };

      request.onerror = (event) => {
        console.error(
          "Error saat mengambil restoran favorit:",
          event.target.error
        ); // Tangkap kesalahan
        reject(event.target.error);
      };
    });
  } catch (error) {
    console.error("Error saat membuka database:", error); // Tangkap kesalahan
    throw error;
  }
};
// Komponen untuk menampilkan daftar restoran favorit
class FavoriteRestaurantList extends HTMLElement {
  async connectedCallback() {
    try {
      // Ambil daftar restoran favorit dari IndexedDB
      const favoriteRestaurants = await fetchFavoriteRestaurants();

      if (favoriteRestaurants && favoriteRestaurants.length > 0) {
        this.renderFavoriteList(favoriteRestaurants);
      } else {
        this.innerHTML = "<p>Anda belum memiliki restoran favorit.</p>"; // Pesan jika tidak ada restoran favorit
      }
    } catch (error) {
      this.innerHTML =
        "<p>Terjadi kesalahan saat mengambil restoran favorit.</p>";
      console.error("Error di FavoriteRestaurantList:", error);
    }
  }

  renderFavoriteList(restaurants) {
    // Start the grid container
    this.innerHTML = `<div class="favorit-grid"></div>`;
    const gridContainer = this.querySelector(".favorit-grid");

    // Gunakan struktur HTML yang mirip dengan daftar restoran

    restaurants.forEach((restaurant) => {
      const favElement = document.createElement("div");
      favElement.classList.add("restaurant-item"); // Add a class for styling

      favElement.innerHTML = `
      <div class="list_item">
      <img 
        class="list_item_thumb" 
        src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" 
        alt="${restaurant.name}" 
      />
      <div class="city">${restaurant.city}</div>
      <div class="list_item_content">
        <div class="list_item_rating">
          <span class="star-icon">&#9733;</span> 
          ${restaurant.rating}
        </div>
        <h1 class="list_item_title">
        <a href="#detail-${restaurant.id}" data-id="${restaurant.id}">${
        restaurant.name
      }</a>
        </h1>
        <div class="list_item_desc">${restaurant.description.slice(
          0,
          150
        )}...</div>
      </div>
    </div>
  `;

      gridContainer.appendChild(favElement);
    });
  }
}

// Daftarkan elemen kustom
customElements.define("favorite-restaurant-list", FavoriteRestaurantList);

