  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
}
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
body {
  font-size: 12px;
  color: #424242;
  background-color: ivory;
}

a {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.menu-seluler {
  background-color: #0c356a;
  color: white;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 20px;
  padding: 10px;
  justify-content: space-between;
}
.nav-seluler {
  background-color: #0c356a;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}
.navlist-seluler {
  width: 60%;
  padding: 0;
  margin: 0;
  text-align: left;
}
.navitems-seluler {
  box-sizing: border-box;
  display: inline-block;
  text-align: right;
  line-height: 24px;
  text-transform: uppercase;
  color: white;
}

.logo-seluler {
  margin-top: 20px;
}
.iconmenu-seluler a {
  text-decoration: none;
  color: white;
  font-size: 49px;
}
.nav-seluler a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #0c356a;
}
.nav-biasa {
  background-color: #0c356a;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}
.navlist {
  width: 80%;
  padding: 0;
  margin: 0;
  text-align: right;
}
.logo {
  width: 20%;
  padding: 0;
  margin: 0;
  text-align: left;
  box-sizing: border-box;
  display: inline-block;
  line-height: 24px;
}
.navitems {
  box-sizing: border-box;
  display: inline-block;
  text-align: right;
  line-height: 24px;
  text-transform: uppercase;
}
.navitems:hover a {
  color: #ff5733; /* Ubah warna teks saat hover di sini */
}

.nav-biasa a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: white;
}
.nav-biasa a:hover {
  text-decoration: none;
  color: yellow;
}

.hero {
  position: relative;
  overflow: hidden;
  text-align: center; /* Mengatur teks ke tengah horizontal */
  max-height: 500px;
}

.hero-image {
  width: 100%;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Gunakan warna gelap semi-transparan */
}

.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.hero-text h1 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.hero-text p {
  font-size: 1rem;
  margin-bottom: 0;
}

main {
  width: 100%;
  margin: 0 auto;
}

.main-grid {
  display: grid;
  grid-template-columns: 3fr; /* Kolom pertama 1 bagian dan kolom kedua 2 bagian */
  gap: 20px; /* Jarak antara kolom */
}

.content {
  margin: 0 20px;
}

.update {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 50px;
  text-align: center;
}
.update h1 {
  font-weight: 700;
  font-size: 2em;
  margin-bottom: 50px;
}

.restaurant-grid,
.favorit-grid {
  text-align: justify;
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Selalu 3 kolom */
  gap: 24px; /* Jarak antar item yang lebih besar untuk pemisahan yang jelas */
  padding: 24px; /* Padding untuk seluruh grid */
  box-sizing: border-box; /* Pastikan padding termasuk dalam perhitungan ukuran */
}

.list_item {
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  /* background-color: white; */
}
.city {
  position: absolute;
  top: 8px;
  left: 16px;
  border-radius: 5px;
  background-color: #df4c2b;
  padding: 5px 10px 5px;
  color: white;
  font-weight: 500;
}

.restaurant-item {
  background: #fff; /* Latar belakang putih untuk kejelasan */
  border: 1px solid #ddd; /* Garis halus untuk pemisahan */
  border-radius: 8px; /* Sudut membulat untuk tampilan modern */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Bayangan halus untuk kedalaman */
  transition: box-shadow 0.3s; /* Transisi untuk efek interaksi */
  overflow: hidden; /* Hindari konten tumpah keluar */
}

.list_item_content {
  padding: 16px; /* Padding yang cukup di dalam konten */
}

.star-icon {
  color: gold; /* Color for star icon (rating) */
}

.restaurant-item:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Bayangan lebih besar saat item dihover */
}

.list_item_thumb {
  width: 100%;
  max-height: 150px;
}
.list_item_rating {
  font-size: 22px;
  text-transform: uppercase;
  color: #999;
  background-color: #0c356a;
  width: 79px;
  font-weight: 500;
  border-radius: 5px;
}
.star-icon {
  color: #ffc400; /* Warna ikon bintang */
  font-size: 25px; /* Ukuran ikon bintang */
  margin-left: 5px;
}
.list_item_rating_value {
  color: #ebe8ff;
  font-weight: bolder;
  text-decoration: none;
  text-align: left;
  padding-top: 5px;
}
.list_item_rating_value a {
  padding-top: 5px;
}
.list_item_title {
  font-weight: 500;
  font-size: 16px;
  margin-top: -10px;
  transition: 0.3s opacity;
}
.list_item_title:hover {
  opacity: 0.5;
}
.list_item_title a {
  text-decoration: none;
  color: inherit;
  padding: 0.8rem 0;
}
#RestaurantList .list_item_desc {
  margin-top: -50px;
  font-size: 12px;
  line-height: 1.5em;
}

#favoriteRestaurantList .list_item_desc {
  font-size: 12px;
  line-height: 1.5em;
}

/* Tampilan untuk halaman detail restoran */
#restaurantDetail {
  padding: 20px;
  background-color: #f8f8f8; /* Warna latar belakang yang lembut */
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Efek bayangan */
  /* margin: 0px auto 20px;  */
  margin-top: -30px;
  margin-bottom: 30px;
  margin-left: 40px;
  margin-right: 40px;
  max-width: 100%; /* Batas maksimum lebar */
  font-size: 16px; /* Ukuran font yang lebih besar */
}

#restaurantDetail h1 {
  text-align: center;
  font-size: 40px;
  background-color: #0c356a;
  color: #fff;
  border-radius: 30px;
  margin-bottom: 20px;
}

/* Gambar restoran di bagian detail */
#restaurantDetail img {
  width: 70%; /* Lebar penuh */
  border-radius: 8px; /* Sudut membulat */
  margin-bottom: 20px; /* Jarak bawah */
  display: block;
  margin-left: auto; /* Agar margin kiri otomatis */
  margin-right: auto; /* Agar margin kanan otomatis */
}

/* Judul restoran */
#restaurantDetail h2 {
  color: #333; /* Warna teks gelap */
  font-size: 40px; /* Ukuran font lebih besar */
  margin-bottom: 10px; /* Jarak bawah */
  margin-left: 40px;
  font-weight: 900;
}
.back-button {
  background-color: #df4c2b;
  color: #fff;
  border: none;
  width: 70px;
  height: 70px;
  min-width: 44px;
  min-height: 44px;
  /* padding: 10px 20px; */
  cursor: pointer;
  border-radius: 50%;
  /* font-size: 16px; */
  margin-left: 40px;
}
.back-button svg {
  min-width: 44px;
  min-height: 44px;
}

.back-button:hover {
  background-color: #008c9e;
}

.detailatas,
.ulasanresto {
  background-color: #ebebeb;
  padding: 15px;
  margin: 20px 40px;
  border-radius: 8px;
}
/* Informasi umum restoran */
#restaurantDetail p {
  color: #666; /* Warna teks lebih ringan */
  margin-bottom: 10px;
  text-align: justify;
}

/* Tombol favorit dasar */
.favorite-button {
  text-align: right;
  background: none; /* Tidak ada latar belakang */
  border: none; /* Tanpa border */
  cursor: pointer; /* Ganti kursor ke pointer */
  color: #ff0000; /* Warna dasar merah */
  font-size: 50px; /* Ukuran lebih besar */
  transition: color 0.3s; /* Efek transisi saat warna berubah */
}

/* Tombol favorit saat dihover */
.favorite-button:hover {
  color: #ff6666; /* Warna saat dihover */
}

/* Tombol favorit ketika restoran ditambahkan ke favorit */
.favorite-button.favorited {
  color: #ff0000; /* Warna merah untuk status favorited */
}

.favorite-button.favorited:hover {
  color: #b80000; /* Warna merah untuk status favorited */
}

.favorite-button.favorited .fa-heart {
  font-weight: bold; /* Ikon tebal */
}

/* Form untuk menambahkan ulasan */
#reviewFormContainer {
  margin: 20px 40px; /* Jarak atas */
  background-color: #ebebeb;
  padding: 15px;
  border-radius: 8px;
}

#reviewFormContainer h3 {
  color: rgb(255, 255, 255); /* Warna teks gelap */
  font-size: 1.5em; /* Ukuran font */
  margin-bottom: 10px; /* Jarak bawah */
  text-align: center;
  background-color: #df4c2b;
  border-radius: 8px;
  padding: 5px 0;
}

/* Elemen input pada formulir ulasan */
#reviewForm input,
#reviewForm textarea {
  width: 100%; /* Lebar penuh */
  padding: 10px; /* Ruang dalam */
  border: 1px solid #ccc; /* Warna border */
  border-radius: 5px; /* Sudut membulat */
  font-size: 1em; /* Ukuran font */
  margin-bottom: 10px; /* Jarak bawah */
}

/* Tombol untuk mengirim ulasan */
#reviewForm button {
  padding: 10px 20px; /* Ruang dalam */
  min-width: 44px;
  min-height: 44px;
  background-color: #0c356a; /* Warna tombol */
  color: #fff; /* Warna teks */
  border: none; /* Tanpa border */
  border-radius: 5px; /* Sudut membulat */
  font-weight: bold; /* Teks tebal */
  cursor: pointer; /* Tampilkan kursor pointer */
  transition: background-color 0.3s; /* Transisi untuk hover */
}

#reviewForm button:hover {
  background-color: #0a2a53; /* Warna saat hover */
}

.ulasanresto h3 {
  color: rgb(255, 255, 255); /* Warna teks gelap */
  font-size: 1.5em; /* Ukuran font */
  margin-bottom: 10px; /* Jarak bawah */
  text-align: center;
  background-color: #df4c2b;
  border-radius: 8px;
  padding: 5px 0;
}

/* Tampilan untuk ulasan pelanggan */
.customer-review {
  padding: 10px; /* Ruang dalam */
  background-color: #fff; /* Warna latar belakang */
  border: 1px solid #ddd; /* Warna border */
  border-radius: 5px; /* Sudut membulat */
  margin-top: 10px; /* Jarak atas */
}

.customer-review p {
  margin: 5px 0; /* Margin atas dan bawah */
  line-height: 1.5; /* Tinggi garis */
}

.customer-review strong {
  color: #333; /* Warna teks gelap */
}

.reviews {
  grid-column: 1; /* Menempatkan ulasan di kolom pertama */
  margin-top: 74px;
  margin-left: 20px;
}

.review {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #0c356a;
}

.review1 {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #ffffff;
}

.reviews h1 {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 2px;
  text-align: center;
  background-color: rgb(255, 196, 0);
  padding: 5px;
  border-radius: 5px;
}

.review1 img {
  border-radius: 50%;
  margin: 0 5px 0px 0;
}

.review-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.customer-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.customer-name {
  margin: 0;
}

.review-date {
  margin: 0;
  color: #888;
  font-size: 14px;
}
.review-rating {
  color: #ffc400;
  font-size: 20px;
}
.review-text {
  margin-top: 10px;
  line-height: 1.5;
}

.skip-link {
  position: absolute;
  top: -100px;
  background-color: #ebe8ff;
  color: #0c356a;
  margin-left: 10px;
  padding: 10px;
  font-size: 20px;
  z-index: 100;
  text-decoration: none;
}
.skip-link:focus {
  top: 100px;
}

#backToHome {
  display: block; /* Pastikan tombol terlihat */
}

footer {
  background-color: #0c356a;
  padding: 15px;
  text-align: center;
  color: rgba(255, 255, 255, 0.664);
}

#splashScreen {
  position: fixed;
  color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #204e8a; /* Warna splash screen */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* Pastikan berada di atas konten utama */
}

.splash-logo {
  width: 100px; /* Ukuran logo */
}

#mainContent {
  display: none; /* Sembunyikan konten utama hingga splash screen hilang */
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
  z-index: 9999; /* Z-index yang tinggi untuk menutupi semua elemen lainnya */
  transition: opacity 0.75s, visibility 0.75s;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #dddddd;
  border-top-color: #0c356a;
  border-radius: 50%;
  animation: loading 0.75s ease infinite;
}

.responsive-hero {
  width: 100%; /* Membuat gambar mengambil lebar penuh kontainernya */
  height: auto; /* Membuat tinggi gambar menyesuaikan secara proporsional */
  max-width: 100%; /* Membuat gambar tidak melebihi ukuran kontainernya */
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

@media screen and (min-width: 500px) {
  .menu-seluler {
    display: none;
  }
  .nav-seluler {
    display: none;
  }
}
@media screen and (max-width: 499px) {
  .restaurant-grid,
  .favorit-grid {
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 16px;
    width: 80%;
    margin-left: 10%;
  }
  .list_item_thumb {
    max-height: 200px; /* Tinggi gambar lebih besar untuk layar kecil */
  }

  .content {
    margin-left: 10px;
    margin-right: 10px;
  }
  .main-grid {
    display: flex;
    flex-direction: column-reverse;
  }
  picture {
    height: 37%;
  }
  .reviews {
    margin-top: 20px; /* Ubah margin sesuai kebutuhan */
    margin-bottom: 20px; /* Ubah margin sesuai kebutuhan */
    margin-left: auto;
    margin-right: auto;
  }
  .reviews h1 {
    text-align: center;
  }

  .content {
    margin-top: 20px; /* Ubah margin sesuai kebutuhan */
    margin-bottom: 20px; /* Ubah margin sesuai kebutuhan */
    margin-left: 10px;
    margin-right: 10px;
    grid-column: initial; /* Hapus aturan grid-column */
  }
  .nav-biasa {
    display: none;
  }
  .nav-seluler {
    z-index: 10;
    background-color: #fff;
    width: 100%;
    position: absolute;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0);
    transition: transform 0.3s ease;
  }
  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  .navitems-seluler {
    display: list-item;
    color: black;
    width: 100%;
    text-align: left;
  }
  .list {
    grid-template-columns: 1fr; /* Mengatur satu kolom untuk layar kecil */
  }
}

@media screen and (min-width: 1200px) {
  .hero-image {
    min-width: 1000px;
    /* height: 100%; */
  }
}
